<template>
  <div class="input-verification-code-container flex">
    <div class="flex-start">
      <p class="label-style">验证码</p>
      <div>
        <el-input
          class="el-input-style"
          placeholder="请输验证码"
          :value="value"
          @input="onInput"
        >
        </el-input>
      </div>
    </div>
    <el-button
      class="btn-style"
      type="text"
      :disabled="disabled"
      @click="onGetVerificationCode"
    >
      {{ disabled ? `重新获取验证码(${timeNum})` : "获取验证码" }}
    </el-button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      // 定时器
      timer: null,
      // 倒计时时间
      timeNum: null,
      // 是否禁用按钮
      disabled: false,
    };
  },

  beforeDestroy() {
    this.cancelDisablButton();
  },

  methods: {
    /**
     * 获取验证码
     */
    onGetVerificationCode() {
      console.log("获取验证码 :>> ");
      this.$emit("send");

      this.disableButton();

      this.timer = setInterval(() => {
        if (this.timeNum <= 1) {
          this.cancelDisablButton();
        }
        this.timeNum--;
      }, 1000);
    },

    /**
     * 禁用按钮
     */
    disableButton() {
      this.disabled = true;
      // 设置倒计时初始时长
      this.timeNum = 60;
    },

    /**
     * 取消禁用
     */
    cancelDisablButton() {
      clearInterval(this.timer);
      this.disabled = false;
    },

    onInput(val) {
      this.$emit("update:value", val);
    },
  },
};
</script>

<style lang="scss" scoped>
.input-verification-code-container {
  padding: px(0) px(12);
  background: #ffffff;
  border-radius: px(8);
  border: px(1) solid #eeeeee;

  .label-style {
    width: px(64);
    font-size: px(14);
    font-weight: bold;
    color: #333333;
  }

  .el-input-style {
    ::v-deep {
      .el-input__inner {
        padding-right: 0;
        border: none;
        overflow: hidden;
      }
    }
  }

  .btn-style {
    width: px(190);
    font-size: px(14);
    font-weight: bold;
    overflow: hidden;
    text-align: right;
  }
}
</style>
